<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">已报名的活动</block>
		</cu-custom>
		
		<view class="cu-card article no-card padding-tb-xs" v-for="(val,i) in activitylist" :key="i">
			<view class="cu-item shadow padding-top" @tap="detail(1)">
				<view class="content">
					<!-- <image class="margin-top-xs" :src="val.trans_pic"></image> -->
					<view class="desc">
						<view class="grid col-6">
							<view>活动：{{val.projectname}}</view>
							<view class="text-right"></view>
						</view>
						<view class="grid col-6">
							<view>时间：{{val.datetime}}</view>
							<view class="text-right"></view>
						</view>
						<view class="grid col-6">
							<!-- <view class="text-grey">04-27 15:04</view> -->
							<view ><text class="text-orange">{{val.statustext}}</text></view>
						</view>
					</view>
				</view>
			</view>
		</view>
		
	</view>
</template>

<script>
	export default {
		mounted() {
			
		},
		onShow() {
			this.init()
		},
		data() {
			return {
				activitylist:[],
				userinfoObj:{}
			};
		},
		methods: {
			async init(){
				this.userinfoObj = uni.getStorageSync('userinfo')
				let that = this
				let obj = {
					userid: this.userinfoObj.id
				}
				let resdata = await new this.Request(this.Urls.m().queryactivitybymy, obj).modepost()
				that.activitylist = resdata.map((v,i)=>{
					v.statustext = v.status
					return v
				})
				
				
			
			},
			detail_bak(state){
				uni.navigateTo({
					url: 'order_detail?state='+state
				})
			}
		}
	}
</script>
<style>
	.cu-card.article>.cu-item .content>image{
		width: 80upx;
		height: 80upx;
	}
</style>

